<template>
  <div class="top-bar">
    <div class="top-bar-content">
      <img src="~assets/img/main/logo.png" alt="logo.png" @click="backToIndex">
      <div class="quit" @click="quit">
        <img src="~assets/img/main/topbar-quit.svg" alt="topbar-quit.svg">
        <p>退出</p>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'TopBar',
  methods: {
    quit(e) {
      if(this.$route.path !== '/') this.$router.push('/')
    },
    backToIndex() {
      if(this.$route.path !== '/') this.$router.push('/main')
    }
  }
  
}
</script>

<style>
  .top-bar-content {
    margin: 0px 16px;
  }
  .top-bar-content img {
    margin: 5px 80px;
    width: 170px;
    height: 50px;
  }
  .quit {
    float: right;
  }
  .quit img {
    margin: 15px 2px;
    width: 32px;
    height: 32px;
  }
  .quit p {
    float: right;
    line-height: 32px;
    color: #000;
  }
</style>