import { request } from './request';
// 获取个人信息
export function getPersonMsg() {
  return request({
    url: '/PersonMessage'
  })
}
// 获取资格设置表格内的详细信息
export function getDescInfo() {
  return request({
    url: '/GuideInfo'
  })
}
// 获取岗位设置与描述的信息
export function getJobDescriptionInfo() {
  return request({
    url: '/JobDescription'
  })
}
// 获取试卷信息
export function getQuestionInfo() {
  return request({
    url: '/QuestionInfo'
  })
}
// 获取错题本的信息
export function getErrorBookInfo() {
  return request({
    url: '/ErrorBook'
  })
}