<template>
  <el-container class="main-el-box">
    <el-header class="main-el-header">
      <top-bar/>
    </el-header>

    <el-container>
      <el-aside class="main-el-aside">
        <nav-bar/>
      </el-aside>

      <el-main class="main-el-main">
        <router-view 
        :caccountMsg="accountMsg"
        :cpersonMsg="personMsg"
        :ccurrentPersonMsg="currentPersonMsg"
        :cdetailInfoList="detailInfoList"
        :cjobDescription="jobDescription"
        :cquestionInfoList="questionInfoList"
        :cquestionInfo="questionInfo"
        :cerrorBookInfo="errorBookInfo"/>

        <img class="hello" src="~assets/img/欢迎界面.png" alt="欢迎界面.png" v-show="this.$route.path==='/main'">
      </el-main>
    </el-container>

  </el-container>
</template>

<script>
  import { getPersonMsg, getJobDescriptionInfo, getDescInfo, getQuestionInfo, getErrorBookInfo } from 'network/main.js'

  import TopBar from './childComps/TopBar.vue'
  import NavBar from './childComps/NavBar.vue'
  export default {
  name: 'Main',
  data() {
    return {
      accountMsg: [],
      personMsg: [],
      currentPersonMsg: {},
      jobDescription: {},
      detailInfoList: {},
      questionInfoList: [],
      questionInfo: {},
      errorBookInfo: {}
    }
  },
  components: {
    TopBar,
    NavBar
  },
  created() {
    getPersonMsg().then(res => {
      this.personMsg = res
      for(let i of res) {
        if(i.id === this.$store.state.currentUserMsg.id) {
          this.currentPersonMsg = i
          let data = this.$store.state.currentUserMsg
          data.direction = i.direction
          this.$emit('addCurrentUserMsg', data)
        }
      }
    }),
    getDescInfo().then(res => {
      this.detailInfoList = res[0]
    }),
    getJobDescriptionInfo().then(res => {
      this.jobDescription = res[0]
    }),
    getQuestionInfo().then(res => {
      this.questionInfoList = res
      for(let i of res) {
        if(i.direction === this.$store.state.currentUserMsg.direction) {
          this.questionInfo = i
        }
      }
    }),
    getErrorBookInfo().then(res => {
      for(let i of res) {
        if(i.id === this.$store.state.currentUserMsg.id) {
          this.errorBookInfo = i
        }
      }
    })
  }
  
}
</script>

<style>
  .el-main {
    height: 100%;
  }
  .main-el-header {
    background: rgba(255, 255, 255, 0.7);
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    margin-bottom: 32px;
  }
  .main-el-aside {
    width: 210px;
  }
  .main-el-main {
    width: calc(100% - 274px);
    margin: 0 calc((274px - 210px) / 2);
    background: rgba(255, 255, 255, 0.7);
  }
  .hello {
    display: block;
    margin: 230px auto;
  }
</style>